.blog-inner {
    .banners {
        margin-top: 0;
        position: relative;
        overflow: hidden;
        
        @media #{$mq-md-max} {
            margin-top: 4.375rem;
        }

        .banner-static {
            width: 100%;
            height: 25.5rem;
            position: relative;
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            .slide-content {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                text-align: center;
                color: $white;
            }

            .titulo {
                font-weight: $bold;
                margin-bottom: 1rem;
                line-height: 1.2;
                color: $white;
                @media #{$mq-md-max} {
                    font-size: 2rem;
                }
                @media #{$mq-sm-max} {
                    font-size: 1.5rem;
                }
            }
            .meta {
                font-size: 1rem;
                color: $white;
                opacity: 0.9;
                margin-bottom: 0.5rem;
                .data, .autor {
                    font-weight: $regular;
                }
            }
        }

        // Remover swiper e slider antigos
        .swiper, .swiper-slide, .swiper-pagination-progress, .slider-nav {
            display: none !important;
        }
    }

    .tag-institucional {
        display: inline-block;
        background: rgba(0, 0, 0, 0.35);
        color: #fff;
        font-size: 1rem;
        font-weight: $medium;
        border-radius: 1rem;
        padding: 0.35rem 1.2rem;
        margin-bottom: 1.2rem;
        letter-spacing: 0.02em;
        backdrop-filter: blur(2px);
    }

    .blog-inner-content {
        padding: 4rem 0;
    }

    .content-row {
        display: flex;
        align-items: flex-start;
        gap: 2rem;
    }
    
    .share-icons {
        display: flex;
        flex-direction: column;
        gap: 1.5rem;
        align-items: center;
        margin-top: 0.5rem;
        
        a {
            text-decoration: none;
            transition: transform 0.3s ease;
            
            &:hover {
                transform: scale(1.1);
            }
        }
        
        .share-icon-circle {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 3.125rem; // 50px
            height: 3.125rem; // 50px
            border-radius: 50%;
            background-color: #0066CC; // Azul
            transition: all 0.3s ease;
            
            svg {
                fill: $white;
                width: 1.25rem; // 20px
                height: 1.25rem; // 20px
            }
        }
        
        a:hover .share-icon-circle {
            background-color: #0052A3; // Azul mais escuro no hover
            box-shadow: 0 0.25rem 1rem rgba(0, 102, 204, 0.3);
        }
    }
    
    .content-description {
        flex: 1;
        font-family: $font-body;

        div{
            p{
                margin: 0;
                color: $black;
    
                @extend .text-medium;
            }
    
            h2{
                color: $black;
                @extend .text-medium-big;
            }
           
        }

       
    }
    
    .content-description img {
        width: 100%;
        border-radius: 1rem;
        margin-top: 1.5rem;
    }
    
    .blog-inner-related {
        padding: 4rem 0;
        background: #f7f7f8;
        .related-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 1rem;
            margin-bottom: 2rem;
            flex-wrap: wrap;
            .related-title {
                margin-bottom: 0;
                margin: 0;
                color: $black;
            }
        }
        .related-btn {
            @extend .text-medium;
            border-radius: 8px;
            font-weight: $bold;
            padding: 0.5rem 1.3rem;
            border: 1.5px solid #1976d2;
            color: #1976d2;
            background: #fff;
            transition: background 0.2s, color 0.2s;
            text-decoration: none;
            &:hover {
                background: #1976d2;
                color: #fff;
            }
        }
        .blog-posts-grid {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 2rem;
            @media (max-width: 991px) {
                grid-template-columns: 1fr 1fr;
            }
            @media (max-width: 600px) {
                grid-template-columns: 1fr;
            }
        }
        .blog-post-card {
            display: flex;
            flex-direction: column;
            background: #fff;
            border-radius: 12px;
            box-shadow: 0 2px 12px rgba(0,0,0,0.06);
            border: 1px solid #e5e5e5;
            overflow: hidden;
            text-decoration: none;
            color: #222;
            transition: box-shadow 0.2s, transform 0.2s;
            &:hover {
                box-shadow: 0 4px 24px rgba(0,0,0,0.12);
                transform: translateY(-2px) scale(1.01);
            }
        }
        .blog-post-image {
            width: 100%;
            aspect-ratio: 16/9;
            overflow: hidden;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                display: block;
            }
        }
        .blog-post-content {
            padding: 1.2rem 1.2rem 1.1rem 1.2rem;
            display: flex;
            flex-direction: column;
            gap: 0.7rem;
        }
        .blog-post-tags {
            display: flex;
            flex-wrap: wrap;
            gap: 0.5rem;
            margin-bottom: 0.3rem;
        }
        .blog-post-tag {
            display: inline-block;
            font-size: 0.95rem;
            font-weight: 500;
            border-radius: 8px;
            padding: 0.3rem 1rem;
            background: #e3eaf6;
            color: #1976d2;
            margin: 0;
            white-space: nowrap;
        }
        .blog-post-tag.audiencia {
            background: #e6f6f2;
            color: #1bb184;
        }
        .blog-post-title {
            font-size: 1.15rem;
            font-weight: 600;
            margin: 0 0 0.2rem 0;
            color: #222;
            line-height: 1.3;
        }
        .blog-post-date {
            font-size: 0.98rem;
            color: #1976d2;
            font-weight: 500;
            margin-top: 0.2rem;
        }
    }
    
    .divisor{
        margin-top: 0;
    }

}


