@import "../variables/colors";
@import "../variables/breakpoints";
@import "../variables/fonts";

.veiculos {
    .banners {
        margin-top: 0;
        position: relative;
        overflow: hidden;
        
        @media #{$mq-md-max} {
            margin-top: 4.375rem;
        }
        
        .swiper {
            width: 100%;
            height: 25.5rem;
        }
        
        .swiper-slide {
            position: relative;
            overflow: hidden;
            
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            
            .slide-content {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                text-align: center;
                color: $white;
            }
            
            .slide-text {
                max-width: 37.5rem;
                color: $white;
                margin-top: 8rem;
                padding-left: 2rem;
                
                @media #{$mq-md-max} {
                    max-width: 28.125rem;
                    margin-top: 5.5rem;
                    padding-left: 1rem;
                }
                
                h2 {
                    font-family: $font-title;
                    font-size: 8rem;
                    font-weight: $bold;
                    line-height: 0.8;
                    margin: 0 0 0.5rem;
                    text-transform: uppercase;
                    letter-spacing: -0.1875rem;
                    
                    span {
                        display: inline-block;
                        font-weight: $light;
                        text-transform: lowercase;
                    }
                    
                    @media #{$mq-lg-max} {
                        font-size: 5.625rem;
                    }
                    
                    @media #{$mq-md-max} {
                        font-size: 4.375rem;
                        margin-bottom: 0.3125rem;
                    }
                    
                    @media #{$mq-sm-max} {
                        font-size: 3.125rem;
                    }
                }
                
                .conectada {
                    font-family: $font-title;
                    font-size: 5rem;
                    font-weight: $light;
                    line-height: 1;
                    margin: 0 0 1.25rem;
                    text-transform: lowercase;
                    letter-spacing: -0.125rem;
                    
                    @media #{$mq-lg-max} {
                        font-size: 3.75rem;
                    }
                    
                    @media #{$mq-md-max} {
                        font-size: 3.125rem;
                        margin-bottom: 0.9375rem;
                    }
                    
                    @media #{$mq-sm-max} {
                        font-size: 2.25rem;
                    }
                }
                
                h3 {
                    font-size: 1.5rem;
                    font-weight: $regular;
                    margin: 0 0 1.563rem;
                    line-height: 1.4;
                    
                    strong {
                        font-weight: $bold;
                    }
                    
                    @media #{$mq-lg-max} {
                        font-size: 1.25rem;
                    }
                    
                    @media #{$mq-md-max} {
                        font-size: 1.125rem;
                        margin-bottom: 1.25rem;
                    }
                    
                    @media #{$mq-sm-max} {
                        font-size: 1rem;
                    }
                }
    
                .badge-wrapper {
                    margin-bottom: 1.563rem;
                    
                    .badge {
                        display: inline-block;
                        background-color: $white;
                        color: $black;
                        font-size: 1rem;
                        font-weight: $semi-bold;
                        padding: 0.5rem 1.125rem;
                        border-radius: 0.25rem;
                        text-transform: uppercase;
                        
                        @media #{$mq-md-max} {
                            font-size: 0.875rem;
                            padding: 0.375rem 0.938rem;
                        }
                        
                        @media #{$mq-sm-max} {
                            font-size: 0.75rem;
                            padding: 0.313rem 0.75rem;
                        }
                    }
                }
                
                .btn-secondary {
                    display: inline-block;
                    padding: 0.75rem 1.563rem;
                    background-color: transparent;
                    border: 0.125rem solid $white;
                    color: $white;
                    text-decoration: none;
                    border-radius: 0.25rem;
                    font-weight: $medium;
                    font-size: 1rem;
                    transition: all 0.3s ease;
                    
                    &:hover {
                        background-color: $white;
                        color: $black;
                    }
                    
                    @media #{$mq-md-max} {
                        padding: 0.625rem 1.25rem;
                        font-size: 0.875rem;
                    }
                }
            }
        }
        
        .swiper-pagination-progress {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 0.375rem;
            background-color: rgba($white, 0.3);
            z-index: 10;
            
            &-fill {
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 0;
                background-color: $green; // Green progress bar using variable
                transition-property: width;
            }
        }
        
        .slider-nav {
            position: absolute;
            bottom: 1.25rem;
            right: 1.25rem;
            display: flex;
            z-index: 10;
            
            .swiper-button-next,
            .swiper-button-prev {
                position: static;
                width: 2.75rem;
                height: 2.75rem;
                background-color: rgba($white, 0.2);
                border-radius: 50%;
                margin: 0 0.313rem;
                color: $white;
                
                &:after {
                    font-size: 1.125rem;
                }
                
                &:hover {
                    background-color: rgba($white, 0.4);
                }
                
                @media #{$mq-sm-max} {
                    width: 2.25rem;
                    height: 2.25rem;
                    
                    &:after {
                        font-size: 1rem;
                    }
                }
            }
        }
    }

    .paginacao {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 0.5rem;
        margin: 3rem 0;
    }

    .paginacao .pagina {
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        border: none;
        background: transparent;
        color: #666;
        font-size: 1rem;
        font-weight: 500;
        cursor: pointer;
        transition: all 0.3s ease;
    }

    .paginacao .pagina:hover {
        background: rgba(0, 102, 204, 0.1);
        color: #0066CC;
    }

    .paginacao .pagina.active {
        background: linear-gradient(90deg, #0066CC 0%, #00CC66 100%);
        color: white;
    }

    .paginacao .pagina.active:hover {
        transform: scale(1.05);
    }

    .paginacao .pagina.next {
        background: #F5F5F5;
    }

    .paginacao .pagina.next:hover {
        background: linear-gradient(90deg, #0066CC 0%, #00CC66 100%);
        color: white;
    }

    .paginacao .pagina.next svg {
        width: 8px;
        height: 12px;
    }

    @media (max-width: 575px) {
        .paginacao .pagina {
            width: 36px;
            height: 36px;
            font-size: 0.875rem;
        }
    }

    .midia-kit-grid {
        margin-top: 2rem;
      }
      
      .midia-kit-box {
        background: #fff;
        border-radius: 1.5rem;
        overflow: hidden;
        box-shadow: 0 0.125rem 0.75rem rgba(0,0,0,0.08);
        padding: 0;
        margin-bottom: 2rem;
        height: 22rem;
        display: flex;
        align-items: stretch;
        justify-content: center;
        transition: transform 0.2s, box-shadow 0.2s;
      }
      
      .midia-kit-box img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 1.5rem;
        box-shadow: 0 0.0625rem 0.375rem rgba(0,0,0,0.06);
      }
      
      .midia-kit-divisor {
        border: none;
        border-top: 0.0625rem solid #e5e5e5;
        margin: 1.5rem 0 2rem 0;
      }
      
      .midia-kit-busca {
        padding: 2rem 0;
      }
      
      .midia-kit-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 2rem;
      }
      
      .midia-kit-header-titulo {
        font-size: 2rem;
        font-weight: 700;
        font-family: 'Montserrat', 'Inter', Arial, sans-serif;
        color: #222;
        margin-bottom: 0;
      }
      
      .midia-kit-filtros {
        display: flex;
        align-items: center;
        gap: 1.25rem;
      }
      
      .midia-kit-filtros .filtro-texto {
        font-size: 1rem;
        color: #333;
        font-weight: 500;
        vertical-align: middle;
      }
      
      .midia-kit-filtros .filtro {
        position: relative;

        &::after {
            content: '';
            position: absolute;
            right: 0.9375rem;
            top: 50%;
            transform: translateY(-50%);
            width: 0.75rem;
            height: 0.75rem;
            background-image: url('../img/arrow-down.svg');
            background-repeat: no-repeat;
            background-size: contain;
            pointer-events: none;
        }

        select {
            appearance: none;
            -webkit-appearance: none;
            -moz-appearance: none;
            padding: 0.75rem 2.1875rem 0.75rem 0.9375rem;
            border: 0.0625rem solid #ddd;
            border-radius: 0.25rem;
            background-color: white;
            font-size: 0.875rem;
            cursor: pointer;
            min-width: 11.25rem;
            vertical-align: middle;

            &:focus {
                outline: none;
                border-color: #007bff;
            }
        }
      }
      
      .midia-kit-filtros .btn-buscar {
        padding: 0.75rem 2rem;
        border: none;
        border-radius: 4px;
        background: linear-gradient(90deg, #0066CC 0%, #00CC66 100%);
        color: white;
        font-size: 1rem;
        font-weight: 600;
        cursor: pointer;
        transition: all 0.3s ease;
      }
      
      .midia-kit-filtros .btn-buscar:hover {
        transform: translateY(-2px);
        box-shadow: 0 4px 12px rgba(0, 102, 204, 0.2);
      }
      
      @media (max-width: 991px) {
        .midia-kit-header {
          flex-direction: column;
          align-items: flex-start;
          gap: 1rem;
        }
        .midia-kit-filtros {
          width: 100%;
          gap: 0.7rem;
        }
      }
      
      .midia-kit-link {
        display: block;
        transition: none;
      }
      .midia-kit-link:hover .midia-kit-box {
        transform: translateY(-0.5rem);
        box-shadow: 0 0.5rem 1.5rem rgba(0,0,0,0.12);
        transition: transform 0.2s, box-shadow 0.2s;
      }
      
      
}

.midia-kits-section {
  padding: 4rem 0;
  .row.align-items-center {
    .col-md-6.text-md-end {
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }
  }
}

.midia-kits-swiper {
  padding-top: 2rem;
}

.midia-kits-swiper .swiper-slide {
  padding-bottom: 2rem;
}

.cases-btn {
  display: inline-block;
  padding: 1rem 1.5rem;
  font-size: 1rem;
  font-weight: 700;
  color: #1976d2;
  background: #fff;
  border: 1px solid #1976d2;
  border-radius: 8px;
  text-decoration: none;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  box-shadow: none;
  text-align: center;

  &:hover,
  &:focus {
    background: #1976d2;
    color: #fff;
    text-decoration: none;
    box-shadow: 0 2px 8px 0 rgba(25, 118, 210, 0.08);
  }
}